<template>
  <div class="container__provisorio">
    <input v-model="value" />
    <div class="container__date">
      {{ date }}
    </div>
  </div>
</template>

<script>
export default {
  props: ['modelValue'],
  emits: ['update:modelValue'],
  data() {
    return {
      date: new Date()
    }
  },
  computed: {
    value: {
      get() {
        return this.modelValue
      },
      set(value) {
        this.$emit('update:modelValue', value)
      }
    }
  }
}
</script>

<style scoped>
.container__provisorio {
  width: 50%;
}
input {
  width: 100%;
  border: none;
  height: 100%;
}
</style>
